.sidenav {
  width: 100%;
  font-family: Favorit,sans-serif;
  background-color:  #e7d6c4;
  position: absolute;
  font-weight: 600;
  height: 100vh;
  outline: none;
  top: -100%;
  border: none;
  transition: all .5s ease-in-out;
  z-index: -1;  
}
.sidenav.active {
  left: 0px;
    top: 45px;
    opacity: 1;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #141414;
    transition: all 0.5s ease-out 0.5s;
    pointer-events: all;
}
.sidenav-menu ul a {
  cursor: pointer;
  opacity: 1;
}
.giHam {
  cursor: pointer;
  font-size: 36px;
  margin-left: 48%;
  z-index: 0;
  fill: #141414;
}
.giHam:active {
  transform: rotate(320deg);
  z-index: 1;
}
.sidenav.active ul   {
  display: flex;
  margin: 5px;
  align-items: center;
  justify-content: space-evenly;
  font-size: 16px;
  padding: 5px;
}
.sidenav-menu a {
  padding: 5px 13px;
  border-radius: 3px;
  color: white;
  text-decoration: none;
}
.sidenav-menu a:hover {
  background-color: #f5ec43;
  color: #141414;
}
.sidenav .name {
  font-size: 20px;
  text-align: center;
  color: white;
  font-weight: 700;
}
.sidenav svg {
  margin-bottom: 30px;
  margin-left: 20%;
  margin-right: 10px;
}
.sidenav svg:hover {
  fill: gray;
}

.layer {
  max-width: 95%;
  padding-left: 3rem;
  width: 90%;
  height: auto;
  margin-top: 5rem;
  z-index: -1;
}

.marquee {
  width: 150%;
  margin-left: -20%;
  background-color: #ddc92a;
  border: 3px solid #141414;
  height: 5vh;
 
  display: flex;
  justify-content: center;
  align-items: center;

}
.marquee span {
  font-size: 1.2rem;
  line-height: 2.5vw;
  letter-spacing: -.03em;
  font-weight: 600;
  text-transform: uppercase;
}

.container {
  height: calc(100vh - 140px);
  width: 100%;
  margin: 0;
}

.footer-grid {
  position: relative;
  left: 0;
  top: auto;
  right: 0;
  width: 100%;
  font-family: Favorit,sans-serif;
  bottom: 0;
  z-index: 3;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding-top: 10vw;
  padding-bottom: 3vw;
  padding-left: 4vw;
  align-items: center;
  justify-content: center;
  background-color: #141414;

}

